@font-face {
  font-family: "National2";
  src: url("../fonts/National2/National2-Light.eot");
  src: url("../fonts/National2/National2-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/National2/National2-Light.woff") format("woff"), url("../fonts/National2/National2-Light.ttf") format("truetype"), url("../fonts/National2/National2-Light.svg#National2") format("svg");
  font-weight: normal;
}
@font-face {
  font-family: "National2-Regular";
  src: url("../fonts/National2/National2-Regular.eot");
  src: url("../fonts/National2/National2-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/National2/National2-Regular.woff") format("woff"), url("../fonts/National2/National2-Regular.ttf") format("truetype"), url("../fonts/National2/National2-Regular.svg#National2-Regular") format("svg");
  font-weight: normal;
}
@font-face {
  font-family: "National2-Bold";
  src: url("../fonts/National2/National2-Bold.eot");
  src: url("../fonts/National2/National2-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/National2/National2-Bold.woff") format("woff"), url("../fonts/National2/National2-Bold.ttf") format("truetype"), url("../fonts/National2/National2-Bold.svg#National2-Bold") format("svg");
  font-weight: normal;
}
@media screen {
  * {
    box-sizing: border-box;
  }
  html {
    height: 100%;
    margin: 0;
    padding: 0;
  }
  body {
    margin: 0;
    word-wrap: break-word;
    background-color: #fff;
    font-family: "National2", verdana, sans-serif;
    font-size: 1.25rem;
    line-height: 1.875rem;
  }
  a:link {
    text-decoration: none;
    color: #328060;
  }
  a:visited {
    color: #328060;
  }
  a:active,
a:hover {
    color: #003c73;
  }
  h1 {
    font-size: 2.1875rem;
    line-height: 3.28125rem;
  }
  h2 {
    font-size: 1.5625rem;
    line-height: normal;
  }
  h3 {
    font-size: 1.40625rem;
  }
  h4 {
    font-size: 1.375rem;
  }
  img {
    max-width: 100%;
    height: auto;
  }
  #skipnav {
    position: fixed;
    z-index: -5;
    top: 1.875rem;
    left: 50%;
    padding: 0.46875rem;
    transform: translateX(-50%);
    opacity: 0;
    border: 1px solid #ababab;
    background-color: #fff;
  }
  #skipnav:focus {
    z-index: 1000;
    opacity: 1;
  }
  .page-wrapper {
    position: relative;
    min-height: 100vh;
  }
  .site-title h1 {
    margin: 0;
    padding-right: 1.875rem;
    font-size: 1.5625rem;
    line-height: normal;
  }
  header {
    position: relative;
    min-height: 4rem;
    margin: 0 0 1.875rem 0;
    padding: 0 5%;
    background-color: #00693e;
  }
  header > * {
    max-width: 1450px;
    margin-right: auto;
    margin-left: auto;
    display: block;
    padding: 0;
  }
  .site-title:hover,
.site-title:active {
    border-bottom: 0;
  }
  header h1 {
    width: 90%;
    margin: 0 0 0.46875rem 0;
    padding: 0.8rem 0;
    line-height: 2.1rem;
  }
  header h1 a,
header h1 a:active,
header h1 a:hover,
header h1 a:focus,
header h1 a:link,
header h1 a:visited {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-column-gap: 1.5rem;
    align-items: center;
    color: #fff;
  }
  header h1 img {
    max-height: 4rem;
  }
  .harmful-content-wrapper {
    margin-top: -1.875rem;
    margin-bottom: 1.875rem;
    padding: 0.25rem;
    text-align: center;
    color: #ffa00f;
    background-color: #000;
  }
  .harmful-content-wrapper a,
.harmful-content-wrapper a:link,
.harmful-content-wrapper a:active,
.harmful-content-wrapper a:visited {
    text-decoration: underline;
    text-underline-offset: 2px;
    opacity: 1;
    color: #ffa00f;
  }
  .harmful-content-wrapper a:hover {
    color: #000;
    background-color: #fff;
  }
  .harmful-content-inline {
    width: 100%;
    margin: 0.5rem 0;
    padding: 0.25rem 1rem;
    color: #fff;
    border-left: 1rem solid #ffa00f;
    background-color: #333;
  }
  .harmful-content-inline a,
.harmful-content-inline a:link,
.harmful-content-inline a:active,
.harmful-content-inline a:visited {
    text-decoration: underline;
    text-underline-offset: 2px;
    opacity: 1;
    color: #fff;
  }
  .harmful-content-inline a:hover {
    color: #000;
    background-color: #fff;
  }
  footer {
    clear: both;
    width: 100%;
    padding: 1rem 0;
    background-color: #333;
  }
  footer > * {
    max-width: 1450px;
    margin-right: auto;
    margin-left: auto;
    width: 90%;
  }
  footer a,
footer a:active,
footer a:focus,
footer a:hover,
footer a:link,
footer a:visited {
    color: #fff;
  }
  footer a:focus,
footer a:hover {
    text-decoration: underline;
  }
  #content.used-for-site-promo .blocks {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-row-gap: 4rem;
    grid-column-gap: 3rem;
    font-size: 1rem;
  }
  #content.not-used-for-site-promo .site-promo-container {
    display: grid;
    grid-template-rows: auto 1fr;
    grid-template-columns: auto 1fr;
    grid-column-gap: 2rem;
    margin-bottom: 2rem;
  }
  #content.not-used-for-site-promo .site-promo-container .site-promo-image {
    grid-row: 1/span 2;
    grid-column: 1;
  }
  #content.not-used-for-site-promo .site-promo-container .site-promo-image figure img {
    width: 250px;
    height: 250px;
    object-position: center center;
  }
  #content.not-used-for-site-promo .site-promo-container .site-promo-harmful-content-flag {
    grid-row: 1/span 2;
    align-self: auto;
  }
  #content.not-used-for-site-promo .site-promo-container h2 {
    grid-row: 1;
    grid-column: 2;
  }
  #content.not-used-for-site-promo .site-promo-container .site-promo-content {
    grid-row: 2;
    grid-column: 2;
  }
  #content {
    max-width: 1450px;
    margin-right: auto;
    margin-left: auto;
    width: 90%;
    padding-bottom: 8rem;
    color: #333;
    font-size: 1.0625rem;
    line-height: normal;
  }
  #content h2 {
    margin-top: 0;
  }
  #content .site-promo-container {
    display: grid;
    grid-template-rows: auto auto 1fr;
    grid-template-columns: 1fr;
  }
  #content .site-promo-container h2 {
    grid-row: 2;
    margin: 0.5rem 0;
    font-size: 1.2rem;
  }
  #content .site-promo-container a,
#content .site-promo-container a:link,
#content .site-promo-container a:visited,
#content .site-promo-container a:hover {
    text-decoration: underline;
    text-underline-offset: 2px;
    opacity: 1;
    color: #00693e;
  }
  #content .site-promo-harmful-content-flag {
    z-index: 2;
    grid-row: 1;
    grid-column: 1;
    align-self: end;
  }
  #content .site-promo-harmful-content-flag p {
    margin: 0;
    padding: 0.5rem;
    text-align: center;
    color: #ffa00f;
    background-color: #000;
    font-weight: bold;
  }
  #content .site-promo-image {
    grid-row: 1;
    grid-column: 1;
    border: none;
  }
  #content .site-promo-image figure {
    margin: 0;
  }
  #content .site-promo-image figure img {
    width: 100%;
    height: 250px;
    margin-top: 0;
    object-fit: cover;
    object-position: left 40%;
  }
  #content .site-promo-image figure figcaption p {
    display: none;
    margin: 0;
    font-size: 0.875rem;
    font-style: italic;
    line-height: normal;
  }
  #content .site-promo-content {
    grid-row: 3;
    grid-column: 1;
  }
  #content .site-promo-content p {
    margin: 0.25rem 0 1rem 0;
  }
  #content p {
    max-width: 1200px;
  }
  #content .blocks .left.file,
#content .blocks .right.file {
    border: none;
  }
  #content .blocks .left.file .item,
#content .blocks .right.file .item {
    padding: 0;
  }
  #content .item-showcase {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 2rem;
    text-align: left;
  }
  #content .item-showcase .resource.item {
    padding: 0 0 2rem 0;
  }
  #content .item-showcase .resource.item:not(:only-child) {
    width: 100%;
  }
  #content .item-showcase .resource.item:not(:only-child) img {
    max-height: 10rem;
  }
  #content .item-showcase .resource.item h3 {
    margin-bottom: 0.5rem;
    line-height: normal;
  }
  #content .item-showcase .resource.item .caption {
    font-size: 1rem;
    line-height: normal;
  }
  #content .item-showcase .resource.item img {
    max-height: 10rem;
  }
  #content .tobii-zoom__icon {
    top: 0;
    right: 0;
    display: flex;
    border: 1px solid #fff;
  }
  .slick-slide #content .tobii-zoom__icon {
    top: 0;
  }
  .display-type-gallery #content .tobii-zoom__icon {
    top: 0;
  }
  .tobii .tobii__slide figure > img {
    max-width: calc(100% - 10em);
    max-height: calc(100% - 4em);
    margin: 0 auto;
  }
  .item.resource.show h3 {
    margin: 1rem 0 0.5rem 0;
    font-size: 1.1rem;
  }
  .item.resource.show .media-embeds {
    float: right;
    width: 33%;
    padding-left: 3rem;
  }
  .item.resource.show .media-embeds.slick-slider {
    width: calc(33% - 3rem);
    margin-left: 3rem;
    padding: 0;
  }
  .item.resource.show .media-embeds.slick-slider img {
    display: block;
    margin: 0 auto;
  }
  .item.resource.show .values,
.item.resource.show .value {
    margin-left: 1rem;
  }
  .item.resource.show .values .value,
.item.resource.show .value .value {
    margin-left: 0;
  }
  ul.resource-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 0;
    column-gap: 2rem;
    list-style: none;
  }
  ul.resource-list li {
    margin-bottom: 4rem;
  }
  ul.resource-list h3 {
    margin: 0.5rem 0;
  }
  ul.resource-list img {
    max-height: 10rem;
  }
  .metadata h3 {
    margin: 0 0 0.25rem 0;
  }
  .metadata .values,
.metadata .value {
    margin-left: 1rem;
  }
  .metadata .values .value,
.metadata .value .value {
    margin-left: 0;
  }
  .browse-controls {
    margin: 1rem 0;
  }
}
@media screen and (max-width: 990px) {
  #content.used-for-site-promo .blocks {
    grid-template-columns: 1fr 1fr;
  }
  #content .item-showcase {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media screen and (max-width: 767px) {
  header h1 {
    font-size: 1.5rem;
    line-height: normal;
  }
  #content h2 {
    font-size: 1rem;
  }
  #content .item-showcase {
    grid-template-columns: 1fr 1fr;
  }
  ul.resource-list {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 575px) {
  header h1 {
    font-size: 1.1rem;
    line-height: normal;
  }
  header h1 img {
    max-height: 2.5rem;
  }
  .blocks .right {
    float: none;
    max-width: 100%;
    margin-left: 0;
  }
  .blocks .left {
    float: none;
    max-width: 100%;
    margin-right: 0;
  }
  #content.used-for-site-promo .blocks {
    grid-template-columns: 1fr;
  }
  #content.used-for-site-promo h2 {
    font-size: 1rem;
  }
  #content.not-used-for-site-promo .site-promo-container {
    grid-template-columns: 1fr;
  }
  #content.not-used-for-site-promo .site-promo-container .site-promo-image {
    grid-row: 1;
  }
  #content.not-used-for-site-promo .site-promo-container h2 {
    grid-row: 2;
    grid-column: 1;
  }
  #content.not-used-for-site-promo .site-promo-container .site-promo-content {
    grid-row: 3;
    grid-column: 1;
  }
  #content .item-showcase {
    grid-template-columns: 1fr;
  }
  .item.resource.show .media-embeds {
    float: none;
    width: 100%;
    padding-left: 0;
  }
  .item.resource.show .media-embeds.slick-slider {
    width: calc(100% - 3rem);
    margin-right: auto;
    margin-left: auto;
  }
  ul.resource-list {
    grid-template-columns: 1fr;
  }
}